import React, { useEffect, useState } from "react";
import Item from "../components/Item";
import { useDispatch, useSelector } from "react-redux";
import { clearSearchTerm, setSearchTerm } from "../state/search.slice";
import axios from "axios";
import Navbar from "../components/Navbar";
import Footer from "../components/Footer";
import CartMenu from "../components/CartMenu";
import Pagination from "../components/Pagination";
import { useNavigate } from "react-router-dom";
import FixedCart from "../components/FixedCart";
import ToggleMenu from "../components/ToggleMenu";
import fruits from "../assets/img/fruit.png";
import marmite from "../assets/img/marmite 2.png";
import truck from "../assets/img/truck.png";
import arrow from "../assets/img/arrow-down.png";

const categories = [
  "Tous les produits",
  "Confitures",
  "Tapas",
  "Sirops",
  "Personnaliss",
];

const Products = () => {
  const searchTerm = useSelector((state) => state.search);
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const menuOpen = useSelector((state) => state.menu.isOpen);
  const [selectedCategory, setSelectedCategory] = useState(categories[0]);
  const [items, setItems] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [postsPerPage] = useState(12);
  const [showAdditionalTextFruits, setShowAdditionalTextFruits] =
    useState(false);
  const [showAdditionalTextTruck, setShowAdditionalTextTruck] = useState(false);
  const [showAdditionalTextMarmite, setShowAdditionalTextMarmite] =
    useState(false);
  const [isSmallScreen, setIsSmallScreen] = useState(window.innerWidth < 1000);

  useEffect(() => {
    axios
      .get(
        "https://server-confiotes.fr/api/items?pagination[page]=1&pagination[pageSize]=100&populate=*"
      )
      .then((res) => {
        setItems(res.data);
        setCurrentPage(1);
      })
      .catch((e) => {
        console.error(e);
      });
  }, [selectedCategory]);

  const currentItems = items.data
    ? items.data
        .filter(
          (item) =>
            selectedCategory === "Tous les produits" ||
            item.attributes.category.includes(selectedCategory)
        )
        .filter(
          (item) =>
            (selectedCategory === "Tous les produits" ||
              item.attributes.category.includes(selectedCategory)) &&
            item.attributes.name
              .toLowerCase()
              .includes(searchTerm.toLowerCase())
        )
    : [];

  const indexOfLastItem = currentPage * postsPerPage;
  const indexOfFirstItem = indexOfLastItem - postsPerPage;
  const currentPosts = currentItems.slice(indexOfFirstItem, indexOfLastItem);

  const scrollToTop = () => {
    window.scrollTo({ top: 0, behavior: "smooth" });
  };

  useEffect(() => {
    const handleResize = () => {
      setIsSmallScreen(window.innerWidth < 1000);
    };
    window.addEventListener("resize", handleResize);
    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);

  const handleCategoryChange = (e) => {
    setSelectedCategory(e.target.value);
    dispatch(clearSearchTerm());
  };

  return (
    <div className="item-list-page">
      <Navbar />
      <div className="bg-img"></div>
      <div className="products-header">
        <h2>Pourquoi choisir nos produits ?</h2>
        <div className="good-choice-container">
          <div className="good-choice">
            <img src={fruits} alt="fruits" className="fruits" />
            <div>
              <p>+ de fruits et  de sucre!</p>
              <br />
              <div
                className="read-more-btn"
                onClick={() =>
                  setShowAdditionalTextFruits(!showAdditionalTextFruits)
                }
              >
                <p>{showAdditionalTextFruits ? "lire moins" : "lire plus"}</p>
                <i
                  className={
                    showAdditionalTextFruits
                      ? "fa-solid fa-circle-arrow-up"
                      : "fa-solid fa-circle-arrow-down"
                  }
                ></i>
                <br />
                <br />
              </div>

              {showAdditionalTextFruits && (
                <div>
                  <p className="additional-text">
                    Pour tout le monde nous vendons des confitures. Or, chez Les
                    Confiotes de Mamie, nous proposons beaucoup de prparation
                    artisanale de fruits  <br />
                    <br />
                    Il est possible que vous ne le sachiez pas, mais la
                    confiture est soumise  des normes strictes en ce qui
                    concerne les pourcentages de sucre et de fruits.
                    <br />
                    <br /> Elle doit contenir un minimum de 55% de sucre aprs
                    cuisson Cette valeur inclut  la fois le sucre
                    naturellement prsent dans le fruit et celui ajout. Cette
                    norme va  l'encontre de ce que l'on pourrait penser,
                    surtout  une poque axe sur une alimentation plus saine!
                    Chez nous cest entre 40% et 50% de sucre en moyenne!{" "}
                    <br />
                    <br />
                    Ct fruits, il faut un minimum de 35%. Nous cuisons nos
                    prparations dans de petit volume, afin de prendre soin
                    deux, et atteindre un taux entre 70% et 90% de fruits!
                  </p>
                </div>
              )}
            </div>
          </div>
          <div className="good-choice">
            <img src={truck} alt="truck" className="truck" />
            <div>
              <p>En collaboration avec nos producteurs locaux</p>
              <br />
              <div
                className="read-more-btn"
                onClick={() =>
                  setShowAdditionalTextTruck(!showAdditionalTextTruck)
                }
              >
                <p>{showAdditionalTextTruck ? "lire moins" : "lire plus"}</p>
                <i
                  className={
                    showAdditionalTextTruck
                      ? "fa-solid fa-circle-arrow-up"
                      : "fa-solid fa-circle-arrow-down"
                  }
                ></i>
                <br />
                <br />
              </div>

              {showAdditionalTextTruck && (
                <div>
                  <p className="additional-text">
                    Lactivit de dpart des Confiotes de Mamie est de proposer
                    un service de transformation aux producteurs locaux de notre
                    rgion (Var). Cela leurs permets dviter la perte dune
                    partie de leur production, et de proposer un produit
                    transform venant directement de leurs champs: les fraises
                    en saisons joignant la confiture de fraise et le sirop de
                    fraise! Une grande volont ANTI GASPI!
                    <br />
                    <br /> Nous travaillons un maximum{" "}
                    <strong>
                      en collaboration avec les producteurs locaux
                    </strong>{" "}
                    en leur achetant leurs fruits et lgumes pour vous proposer{" "}
                    <strong>le meilleur du got en circuit-court!</strong>
                  </p>
                </div>
              )}
            </div>
          </div>
          <div className="good-choice">
            <img src={marmite} alt="marmite" className="marmite" />
            <div>
              <p>Cuisson au chaudron en cuivre</p>
              <br />
              <div
                className="read-more-btn"
                onClick={() =>
                  setShowAdditionalTextMarmite(!showAdditionalTextMarmite)
                }
              >
                <p>{showAdditionalTextMarmite ? "lire moins" : "lire plus"}</p>
                <i
                  className={
                    showAdditionalTextMarmite
                      ? "fa-solid fa-circle-arrow-up"
                      : "fa-solid fa-circle-arrow-down"
                  }
                ></i>
                <br />
                <br />
              </div>

              {showAdditionalTextMarmite && (
                <div>
                  <p className="additional-text">
                    Chez Les Confiotes de Mamie toutes nos confitures sont
                    cuites au chaudron en cuivre, comme  lancienne, on
                    respecte les traditions ! Et cela comporte de nombreux
                    avantages : <br />
                    <br />
                    <strong>Qualit de la cuisson :</strong> La cuisson au
                    chaudron en cuivre permet une rpartition uniforme de la
                    chaleur, favorisant une cuisson homogne des fruits et une
                    prservation optimale de leurs saveurs. <br />
                    <br />
                    <strong>Texture et consistance amliores :</strong> La
                    raction du cuivre avec les protines des fruits peut
                    contribuer  crer une texture plus dlicate et une
                    consistance agrable, donnant aux confitures une qualit
                    artisanale.
                    <br />
                    <br /> <strong>Got amlior :</strong> La ractivit du
                    cuivre avec les ingrdients peut influencer positivement le
                    got des confitures, crant des nuances de gots qui ne
                    seraient pas obtenus avec d'autres mthodes de cuisson.
                  </p>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
      <div className="item-list-nav">
        {isSmallScreen ? (
          <div className="select-container">
            <select
              value={selectedCategory}
              onChange={handleCategoryChange}
              className="category-select custom-select"
            >
              {categories.map((category) => (
                <option key={category} value={category}>
                  {category}
                </option>
              ))}
            </select>
          </div>
        ) : (
          <ul>
            {categories.map((category) => (
              <li key={category}>
                <input
                  type="radio"
                  id={category}
                  checked={category === selectedCategory}
                  onChange={(e) => setSelectedCategory(e.target.id)}
                />
                <label
                  htmlFor={category}
                  onClick={() => dispatch(clearSearchTerm())}
                  className={category === selectedCategory ? "selected" : ""}
                >
                  {category}
                </label>
              </li>
            ))}
          </ul>
        )}
        <div className="search-input">
          <input
            type="text"
            placeholder="Rechercher"
            value={searchTerm}
            onChange={(e) => dispatch(setSearchTerm(e.target.value))}
          />
          <i
            className="fa-solid fa-magnifying-glass"
            onClick={() => navigate("/products")}
          ></i>
        </div>
      </div>
      <div className="item-list-container">
        <div className="item-list">
          {currentPosts.length > 0 ? (
            currentPosts.map((item) => (
              <Item
                item={item}
                id={item?.id}
                key={item?.id}
                url={item?.attributes?.image?.data?.attributes?.url}
                name={item?.attributes?.name}
                price={item?.attributes?.price}
                weight={item?.attributes?.weight}
                available={item?.attributes?.available}
              />
            ))
          ) : (
            <div className="empty-msg">
              Aucun produit ne correspond  la recherche.
            </div>
          )}
        </div>
      </div>
      <Pagination
        totalPosts={currentItems.length}
        postsPerPage={postsPerPage}
        setCurrentPage={(page) => {
          scrollToTop();
          setCurrentPage(page);
        }}
        currentPage={currentPage}
      />
      <CartMenu />
      {menuOpen ? <ToggleMenu menuOpen={menuOpen} /> : ""}
      <FixedCart />
      <Footer />
    </div>
  );
};

export default Products;
