import React, { useEffect, useState } from "react";
import axios from "axios";
import Navbar from "../components/Navbar";
import CartMenu from "../components/CartMenu";
import { useDispatch, useSelector } from "react-redux";
import { clearCart } from "../state/cart.slice";
import Footer from "../components/Footer";
import { useNavigate } from "react-router-dom";
import ToggleMenu from "../components/ToggleMenu";

const Success = () => {
  const storedId = localStorage.getItem("storedId");
  const menuOpen = useSelector((state) => state.menu.isOpen);
  const dispatch = useDispatch();
  const [orderData, setOrderData] = useState("");
  const navigate = useNavigate();

  useEffect(() => {
    const successOrder = async () => {
      try {
        // tape 1 : Rcuprer la liste des commandes depuis l'API
        const res = await axios.get(`https://server-confiotes.fr/api/orders`);

        // tape 2 : Trouver la commande correspondant  la stripeSessionId
        const matchingOrder = res.data.data.find(
          (order) => order.attributes.stripeId === storedId
        );

        if (matchingOrder) {
          // tape 3 : Mettre  jour l'tat de la commande
          const updateData = {
            ...matchingOrder.attributes,
            payed: true,
          };
          await axios.put(
            `https://server-confiotes.fr/api/orders/${matchingOrder.id}`,
            { data: updateData }
          );

          // tape 4 : Rcuprer la commande mise  jour
          const updatedOrderResponse = await axios.get(
            `https://server-confiotes.fr/api/orders/${matchingOrder.id}`
          );

          // tape 5 : Mettre  jour l'tat local avec la commande mise  jour
          setOrderData(updatedOrderResponse.data.data);
        } else {
          console.log("No matching order found.");
        }

        // tape 9 : Nettoyer le stockage local et le panier
        localStorage.removeItem("cart");
        localStorage.removeItem("storedId");
        dispatch(clearCart());
      } catch (error) {
        // Grer les erreurs
        console.error("Error updating order:", error);
      }
    };

    // Appeler la fonction pour dclencher le processus
    successOrder();
  }, [storedId, dispatch]);

  // tape 8 : Envoyer l'e-mail (en dehors du useEffect, une seule fois)
  useEffect(() => {
    if (orderData) {
      const orderSummary = orderData?.attributes?.products
        .map((product) => {
          return `${product.name} (${product.count})`;
        })
        .join("\n");

      const mailData = {
        mailConfiotes: "lesconfiotesdemamie@gmail.com",
        mailCustomer: orderData?.attributes?.userData.email,
        objet: `Les Confiotes de Mamie - Commande N ${orderData.id}`,
        content: `
          Bonjour ${orderData?.attributes?.userData?.username} ${
          orderData?.attributes?.userData?.lastname
        },

          Merci d'avoir choisi Les Confiotes de Mamie ! Nous avons bien reu votre commande n${
            orderData.id
          } d'un montant de ${orderData?.attributes?.price.toFixed(2)}.

          Rcapitulatif de votre commande :
          ${orderSummary}

          Votre commande sera traite dans les plus brefs dlais (24h-48h). Vous recevrez un e-mail de confirmation d'expdition de la part du transporteur une fois que votre colis sera en route.

          Si vous avez des questions ou besoin d'une facture, n'hsitez pas  nous contacter  l'adresse lesconfiotesdemamie@gmail.com ou par tlphone au 06 46 41 31 59.

          Nous vous remercions de votre confiance.

          Bien cordialement,
          L'quipe des Confiotes de Mamie
        `,
      };

      // tape 8 : Envoyer l'e-mail
      axios
        .post("https://server-confiotes.fr/api/emails", mailData, {
          headers: {
            "Content-Type": "application/json",
          },
        })
        .then(() => {
          console.log("Email sent.");
        })
        .catch((error) => {
          console.error("Error sending email:", error);
        });
    }
  }, [orderData]);

  return (
    <>
      <div className="success-page">
        <Navbar />
        <CartMenu />
        <div
          className="success-container
        "
        >
          <h2>Confirmation de commande :</h2>
          <div className="order-confirmation-container">
            <i class="fa-solid fa-check"></i>
            <div className="message-container">
              <h3>Merci pour votre commande !</h3>
              <p>
                Nous avons bien reu votre commande, un email de confirmation va
                vous tre envoy avec les dtails de cette dernire.
              </p>
            </div>
          </div>
          <button onClick={() => navigate("/")}>Accueil</button>
        </div>
      </div>
      {menuOpen ? <ToggleMenu menuOpen={menuOpen} /> : ""}
      <Footer />
    </>
  );
};

export default Success;
